import React from 'react';
import './StatsSection.scss';

function StatsSection() {
    return (
        <section className="stats-section">
            <div className="stats-section-inner">
                <div className="section-card">
                    <span className="section-card-label label-medium">21 de cmine de reedin</span>
                    <p className="section-card-paragraph paragraph-secondary">Campusul are o suprafa de 137.148 mp, aproximativ 14 ha. Primele cmine au fost construite in anul 1969.</p>
                </div>
                <div className="section-card">
                    <span className="section-card-label label-medium">8.000 de locuri de cazare</span>
                    <p className="section-card-paragraph paragraph-secondary">Campusul este alimentat de echipamente termice de ultim generaie, iar cminele sunt reabilitate.</p>
                </div>
                <div className="section-card">
                    <span className="section-card-label label-medium">Preuri avantajoase</span>
                    <p className="section-card-paragraph paragraph-secondary">n Tudor, te bucuri de cele mai mici tarife de cazare din ar.</p>
                </div>
                <div className="section-card">
                    <span className="section-card-label label-medium">11 organizaii studeneti</span>
                    <p className="section-card-paragraph paragraph-secondary">Exista i dou organizaii la nivel internaional: BEST i AIESEC TUIAI.</p>
                </div>
            </div>
        </section>
    )
}

export default StatsSection;
